
.fileSelectUpButton {
  display: inline-block;
  height: 100%;
  width: 100%;
  font-size: 20px;
  margin-top: -2px;
  position: absolute;
}
.upLoaderFile {
  position: relative;
  text-align: center;
  vertical-align: middle;
  line-height: 28px;
  height: 28px;
  width: 70px;
  display: inline-block;
  background-color: #ADCD00;
  border-radius: 5px;
}
.upLoaderFile .textWord {
  color: #fff;
}
.dishidden {
  opacity: 0;
}



